<script>
    import { mobileCheck } from './core/Mobile.js'
    export let data = {
        id: "",
        number: null,
        text: null
    };

    let idx, len;
    if (data.number) {
        idx = 0;
        len = data.number.length - 1;
    }

    function getImage(folder, index) {
        return "https://alans-photos.s3-us-west-1.amazonaws.com/" + folder + "/" + index.toString() + ".jpg";
    }

    function prev() {
        if (idx > 0) idx--;
        else idx = len;
    }

    function next() {
        if (idx < len) idx++;
        else idx = 0;
    }

    let isMobile = mobileCheck();
</script>

<style src="../styles/Gallery.scss">/* NON-LAPTOP/DESKTOP LAYOUT */
img {
  max-width: 100%;
  max-height: 100%;
  margin-top: 15%;
  overflow: hidden; }

a {
  cursor: pointer;
  text-decoration: underline; }

h2 {
  display: block; }

/* tablet layout */
@media screen and (min-width: 600px) {
  p {
    font-size: 20px; } }

/* tablet pro layout */
@media screen and (min-width: 900px) {
  p {
    font-size: 28px; } }

/* laptop layout */
@media screen and (min-width: 1200px) {
  h2 {
    margin-top: 3%;
    margin-left: 26.75%;
    font-size: 0.8em; }
  p {
    margin-left: 28%;
    font-size: 14px;
    width: 38em; }
  img {
    max-width: 70%;
    max-height: 700px;
    margin-top: 5%;
    /* -5% */
    margin-left: 28%;
    /* -17% */ } }

/* desktop layout */
@media screen and (min-width: 1800px) {
  h2 {
    margin-top: -1.5%;
    margin-left: 26.2%;
    font-size: 1.0em; }
  p {
    margin-left: 27%;
    font-size: 20px;
    width: 36em; }
  img {
    max-width: 70%;
    max-height: 900px;
    margin-top: 8%;
    margin-left: 27%;
    display: block; } }

/*# sourceMappingURL=Gallery.svelte.css.map */</style>

{#if isMobile}
    {#each data.number as index}
        <img alt="" src={getImage(data.id, index)}/>
    {/each}
{:else}
    {#if !data.text}
        <h2><a on:click={prev}>Previous</a> / <a on:click={next}>Next</a> ({idx + 1} of {len + 1})</h2>
    {/if}
    
    {#if !isMobile && data.text}
        <img alt="" on:click={next} style="max-width: 40%; max-height: 400px;" src={getImage(data.id, idx)}/>
    {:else}
        <img alt="" on:click={next} src={getImage(data.id, idx)}/>
    {/if}
{/if}

{#if data.text}
    {#each data.text as blurb}
        <p>{blurb}</p>
    {/each}
{/if}
